.container {
  margin: 0 auto;
  padding: 0 16px;
}

.fav-hero-section {
  padding-top: 24px;
  padding-bottom: 40px;
}

.fav-hero-container {
}

.fav-hero-pic {
  height: 516px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .fav-hero-section {
    padding-top: 22px;
    padding-bottom: 64px;
  }

  .fav-hero-pic {
    height: 356px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 100px;
    padding-right: 100px;
  }

  .fav-hero-section {
    padding-top: 20px;
  }

  .fav-hero-pic {
  }
}
